<!--  -->
<template>
  <div>
     <div class="card__wrapper">

        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="title">求职意向</span>
          </div>

          <div class="p">
            <span>
               尊敬的领导：
            </span>
            </div>
          <div >
             我是湖南农业大学电子商务专业的学生蒲茜。毕业将至，又一个新的开始即将来来到，等待着我继续努力奋斗、迎接挑战。时光飞梭，将带着童年的梦想、青年的理想离开学校，走上工作岗位。大学四年是我的思想、知识结构及心理、生长成熟的四年。惠于农业大学的浓厚学习、创新氛围，熔融其中四年使我成为一名复合型人才。
  在校的主要任务是学习，所以我掌握了较强的专业知识，并把理论知识运用到实践中去，期末总评成绩名列年级前，荣获校三好学生称号。我所学专业是电子商务，对电脑有着近乎狂热的喜爱。假期中，我根据专业特长，在互联网公司参加社会实践，这对我的经验积累起到了极其重要的作用。并考取了计算机初级证书，对即将步入社会的我，充满了信心。
  “长风破浪会有时，直挂云帆济沧海”，怀着饱满的热情、坚韧的性格、勤奋的态度，等待着我的是机遇与挑战！抓住这个机会，为单位尽自己绵薄之力。
          </div>

           <div class="p">
          </div>
           <div class="p">
            <span>此致</span>
          </div>
           <div class="p">
            <span>敬礼</span>
          </div>
           <div class="p">
            <span>求职人：蒲茜</span>
          </div>
        </el-card>
      </div>
  </div>
</template>

